<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process124"></a>

<p><br></p>
<p><span class="rvts17">章节简介</span></p>
<p class="rvps2"><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1039.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在逃离了欢乐堡之后，接下来的任务是继续朝东北方向，寻找码头。然而等待的将是更加残酷的战斗。</span>
<br><span class="rvts21">寻找盟友，提升装备和等级，应战驻守在码头的亚历山大部队。需要逃离这片血腥的陆地，摆脱噩梦般的挣扎。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



